import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { ChevronDown } from "lucide-react";

export type SortOption = {
  value: string;
  label: string;
};

interface SortDropdownProps {
  options: SortOption[];
  selectedSort: string;
  onSortChange: (value: string) => void;
}

export const SortDropdown = ({
  options,
  selectedSort,
  onSortChange,
}: SortDropdownProps) => {
  const selectedOption = options.find(option => option.value === selectedSort);

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button
          variant="outline"
          className="flex items-center gap-2 border-collection-filter-border bg-collection-filter-bg hover:bg-collection-card-hover min-w-[200px] justify-between"
        >
          <span className="text-sm">
            {selectedOption?.label || "Sort by"}
          </span>
          <ChevronDown className="w-4 h-4" />
        </Button>
      </DropdownMenuTrigger>
      
      <DropdownMenuContent align="end" className="w-[200px]">
        {options.map((option) => (
          <DropdownMenuItem
            key={option.value}
            onClick={() => onSortChange(option.value)}
            className={`text-sm cursor-pointer ${
              option.value === selectedSort 
                ? "bg-accent text-accent-foreground" 
                : ""
            }`}
          >
            {option.label}
          </DropdownMenuItem>
        ))}
      </DropdownMenuContent>
    </DropdownMenu>
  );
};