import { useState } from "react";
import { Heart, ShoppingBag, ChevronLeft, ChevronRight } from "lucide-react";
import { Button } from "@/app/Components/ui/button";
import { Badge } from "@/app/Components/ui/badge";

interface ProductCardProps {
  id: string;
  name: string;
  price: string;
  images: string[];
  sizes: string[];
  isNew?: boolean;
  isWishlisted?: boolean;
  onWishlistToggle?: (id: string) => void;
  onAddToCart?: (id: string, size: string) => void;
}

export const ProductCard = ({
  id,
  name,
  price,
  images,
  sizes,
  isNew = false,
  isWishlisted = false,
  onWishlistToggle,
  onAddToCart,
}: ProductCardProps) => {
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const [isHovered, setIsHovered] = useState(false);
  const [showSizes, setShowSizes] = useState(false);

  const nextImage = (e: React.MouseEvent) => {
    e.stopPropagation();
    setCurrentImageIndex((prev) => (prev + 1) % images.length);
  };

  const prevImage = (e: React.MouseEvent) => {
    e.stopPropagation();
    setCurrentImageIndex((prev) => (prev - 1 + images.length) % images.length);
  };

  const handleSizeClick = (size: string, e: React.MouseEvent) => {
    e.stopPropagation();
    onAddToCart?.(id, size);
    setShowSizes(false);
  };

  return (
    <div 
      className="group relative bg-collection-card rounded-sm overflow-hidden transition-all duration-300 hover:bg-collection-card-hover"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => {
        setIsHovered(false);
        setShowSizes(false);
      }}
    >
      {/* Product Image */}
      <div className="relative aspect-[3/4] overflow-hidden">
        <img
          src={images[isHovered && images.length > 1 ? (currentImageIndex + 1) % images.length : currentImageIndex]}
          alt={name}
          className="w-full h-full object-cover transition-all duration-500 group-hover:scale-105"
        />
        
        {/* Image Navigation */}
        {images.length > 1 && (
          <>
            <Button
              variant="ghost"
              size="icon"
              className="absolute left-2 top-1/2 -translate-y-1/2 w-6 h-6 bg-background/80 backdrop-blur-sm hover:bg-background/90 opacity-0 group-hover:opacity-100 transition-all duration-200"
              onClick={prevImage}
            >
              <ChevronLeft className="w-3 h-3" />
            </Button>
            <Button
              variant="ghost"
              size="icon"
              className="absolute right-2 top-1/2 -translate-y-1/2 w-6 h-6 bg-background/80 backdrop-blur-sm hover:bg-background/90 opacity-0 group-hover:opacity-100 transition-all duration-200"
              onClick={nextImage}
            >
              <ChevronRight className="w-3 h-3" />
            </Button>
          </>
        )}
        
        {/* New Badge */}
        {isNew && (
          <Badge className="absolute top-3 left-3 bg-collection-badge-new text-collection-badge-new-foreground hover:bg-collection-badge-new/90 text-xs font-medium px-2 py-1">
            New
          </Badge>
        )}
        
        {/* Wishlist Button */}
        <Button
          variant="ghost"
          size="icon"
          className="absolute top-3 right-3 w-8 h-8 bg-background/80 backdrop-blur-sm hover:bg-background/90 transition-all duration-200"
          onClick={() => onWishlistToggle?.(id)}
        >
          <Heart
            className={`w-4 h-4 transition-colors ${
              isWishlisted ? "fill-destructive text-destructive" : "text-muted-foreground"
            }`}
          />
        </Button>
        
        {/* Size Selector on Hover */}
        {showSizes && (
          <div className="absolute bottom-0 left-0 right-0 bg-background/95 backdrop-blur-sm p-3 transform translate-y-0 transition-all duration-300">
            <div className="flex flex-wrap gap-2 justify-center">
              {sizes.map((size) => (
                <Button
                  key={size}
                  variant="outline"
                  size="sm"
                  className="h-8 px-3 text-xs border-muted-foreground/20 hover:border-foreground hover:bg-foreground hover:text-background"
                  onClick={(e) => handleSizeClick(size, e)}
                >
                  {size}
                </Button>
              ))}
            </div>
          </div>
        )}
        
        {/* Add to Cart Button - Shows on hover */}
        <Button
          variant="ghost"
          size="icon"
          className="absolute bottom-3 right-3 w-8 h-8 bg-background/80 backdrop-blur-sm hover:bg-background/90 transition-all duration-200 opacity-0 group-hover:opacity-100 translate-y-2 group-hover:translate-y-0"
          onClick={() => setShowSizes(!showSizes)}
        >
          <ShoppingBag className="w-4 h-4 text-muted-foreground" />
        </Button>
      </div>
      
      {/* Product Info */}
      <div className="p-4 space-y-2">
        <h3 className="text-sm font-medium text-foreground leading-tight line-clamp-2">
          {name}
        </h3>
        <p className="text-sm font-semibold text-collection-price-text">
          {price}
        </p>
      </div>
    </div>
  );
};